import { useSpring } from '@react-spring/core'
import { animated, a as three } from '@react-spring/three'
import { a as web } from '@react-spring/web'
import { Environment, OrbitControls, Stats, useGLTF, useHelper } from '@react-three/drei'
import { Canvas } from '@react-three/fiber'
import React, { Suspense, useEffect, useRef, useState } from 'react'
import * as THREE from 'three'

function Model({ open, ...props }) {
  const { nodes, materials } = useGLTF('https://raw.githubusercontent.com/dev-gian/dextall-3d/main/dextall-win-transformed.glb')
  // Take care of cursor state on hover
  const [hovered, setHovered] = useState(false)
  const positions = useSpring({
    1: [0.333, -0.072, open ? 2 : 0],
    2: [0.18, -0.115, open ? 0 : 0],
    3: [0, 0, open ? 3 : 1.749],
    4: [0.013, -0.023, open ? 1.33 : 1.33],
    5: [0, -0.06, open ? 2 : 1.574],
    6: [0, 0, open ? 1 : 0.1]
  })

  const dirLight = useRef(null)
  useHelper(dirLight, THREE.DirectionalLightHelper, 1, 'red')
  useEffect(() => void (document.body.style.cursor = hovered ? 'pointer' : 'auto'), [hovered])
  return (
    <group {...props} dispose={null}>
      <animated.mesh geometry={nodes['3_rain-screen-cladding'].geometry} material={materials.PaletteMaterial001} position={positions[3]} scale={0.01} />
      <mesh geometry={nodes.metals.geometry} material={materials.PaletteMaterial001} position={[0.061, -0.082, 1.641]} scale={0.01} />
      <group position={[0.076, -0.024, 1.434]} rotation={[0, 0, -Math.PI / 2]} scale={0.01}>
        <mesh geometry={nodes.Solid157.geometry} material={materials.PaletteMaterial001} />
        <mesh geometry={nodes.Solid157_1.geometry} material={materials.PaletteMaterial001} />
      </group>
      <animated.mesh geometry={nodes['4_sheet-metal-vapor'].geometry} material={materials.GalvanizedSteel01_4K_} position={positions[4]} scale={0.01} />
      <animated.mesh
        geometry={nodes['5_gaskets'].geometry}
        material={materials.PaletteMaterial001}
        position={positions[5]}
        rotation={[0, -Math.PI / 2, 0]}
        scale={0.01}
      />
      <group position={[0, 0.092, 1.638]} scale={0.01}>
        <mesh geometry={nodes.gipsem001.geometry} material={materials.PaletteMaterial001} />
        <mesh geometry={nodes.gipsem001_1.geometry} material={materials.PaletteMaterial001} />
      </group>
      <group position={[0, 0.093, 1.449]} scale={0.01}>
        <mesh geometry={nodes.insulation001.geometry} material={materials.PaletteMaterial001} />
        <mesh geometry={nodes.insulation001_1.geometry} material={materials.Material__2097643234} />
      </group>
      <group position={[-0.006, -0.561, 1.67]} scale={0.01}>
        <mesh geometry={nodes.Object048.geometry} material={materials.PaletteMaterial002} />
        <mesh geometry={nodes.Object048_1.geometry} material={materials.PaletteMaterial001} />
        <mesh geometry={nodes.Object048_2.geometry} material={materials.PaletteMaterial001} />
      </group>
    </group>
  )
}

export default function App() {
  // This flag controls open state, alternates between true & false
  const [open, setOpen] = useState(false)
  // We turn this into a spring animation that interpolates between 0 and 1
  const props = useSpring({ open: Number(open) })
  return (
    <web.main style={{ background: props.open.to([0, 1], ['#f0f0f0', '#dedeee']) }}>
      <web.h1 style={{ opacity: props.open.to([0, 1], [1, 0]), transform: props.open.to((o) => `translate3d(-50%,${o * 50}px,0)`) }}>click</web.h1>
      <Canvas dpr={[1, 2]} camera={{ position: [6, 3, 3] }}>
        <OrbitControls />
        <Stats />
        <three.pointLight position={[0, 40, 20]} intensity={3} color={props.open.to([0, 1], ['#f0f0f0', '#eaeaff'])} />
        <Suspense fallback={null}>
          <group position={[-1.125, 1.75, -3]} onClick={(e) => (e.stopPropagation(), setOpen(!open))}>
            <Model open={open} />
          </group>
          <Environment preset="city" />
          <axesHelper args={[5]} />
          <gridHelper />
        </Suspense>
      </Canvas>
    </web.main>
  )
}
